<template>
  <div v-if="loading" class="">
    <LoadSkeletonFilter></LoadSkeletonFilter>
  </div>
  <div v-else class="content">
    <div class="filters-box mb-4 py-2 bg-white dark:bg-gray-800">
      <ul>
        <li>
          <div
            class="filter-name mt-2 text-sm md:text-lg font-bold text-gray-800 dark:text-white"
          >
            分类：
          </div>
          <div class="filter-items flex gap-3 overflow-x-auto">
            <div
              v-for="(item, index) in video_types"
              :key="index"
              :class="[item === video_type ? 'active' : '']"
              @click="changeVideoType(item)"
              class="text-sm md:text-lg break-keep speed-item p-1 px-2 rounded-sm bg-gray-200 text-gray-800 dark:text-white"
            >
              {{ item }}
            </div>
          </div>
        </li>
        <li>
          <div
            class="filter-name mt-2 text-sm md:text-lg font-bold text-gray-800 dark:text-white"
          >
            标签：
          </div>
          <div
            class="filter-items h-full flex items-center gap-3 overflow-x-auto"
          >
            <div
              v-for="(item, index) in video_tags"
              :key="index"
              @click="changeVideoTag(item)"
              :class="[item === video_tag ? 'active' : '']"
              class="text-sm md:text-lg break-keep speed-item p-1 px-2 rounded-sm bg-gray-200 dark:bg-gray-800 text-gray-800 dark:text-white"
            >
              {{ item }}
            </div>
          </div>
        </li>
        <li>
          <div
            class="filter-name mt-2 text-sm md:text-lg font-bold text-gray-800 dark:text-white"
          >
            年代：
          </div>
          <div
            class="filter-items flex items-center gap-3 h-full overflow-x-auto"
          >
            <div
              v-for="(item, index) in video_years"
              :key="index"
              @click="changeVideoYear(item)"
              :class="[item === video_year ? 'active' : '']"
              class="text-sm md:text-lg break-keep speed-item p-1 px-2 rounded-sm bg-gray-200 dark:bg-gray-800 text-gray-800 dark:text-white"
            >
              {{ item }}
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="list-content">
      <div
        class="content-video-list mb-8 grid grid-cols-3 md:grid-cols-8 gap-3"
      >
        <router-link
          :to="'/watch/' + line.ID"
          v-for="(line, number) in data"
          :key="number"
          class="video-item-card h-60 brightness-50 w-full rounded-sm object-cover object-center"
        >
          <img
            loading="lazy"
            class="video-item-card-image rounded-sm brightness-50"
            :src="line.image"
            alt=""
          />
          <div
            class="video-item-card-title mt-2 text-sm font-bold text-gray-800 dark:text-white"
          >
            {{ line.title }}
          </div>
          <div
            class="video-item-card-heard px-1 py-1 w-full absolute top-0 left-0"
          >
            <div
              class="item-card-heard-content flex items-center justify-between"
            >
              <div class="item-card-heard-speed text-sm text-gray-200">
                {{ COMMON.getSpeed(line) }}
              </div>
              <div class="item-card-heard-score text-sm text-yellow-300">
                {{ line.score + "分" }}
              </div>
            </div>
          </div>
        </router-link>
      </div>
      <div
        class="sticky bottom-0 right-0 py-4 items-center w-full bg-white border-gray-200 sm:flex sm:justify-between dark:bg-gray-800 dark:border-gray-700"
      >
        <div class="flex items-center mb-4 sm:mb-0">
          <div
            class="inline-flex justify-center p-1 text-gray-500 rounded cursor-pointer hover:text-gray-900 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-700 dark:hover:text-white"
          >
            <svg
              @click="BackPage()"
              class="w-7 h-7"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                clip-rule="evenodd"
              ></path>
            </svg>
          </div>
          <div
            class="inline-flex justify-center p-1 mr-2 text-gray-500 rounded cursor-pointer hover:text-gray-900 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-700 dark:hover:text-white"
          >
            第{{ page }}页
          </div>
          <div
            class="inline-flex justify-center p-1 mr-2 text-gray-500 rounded cursor-pointer hover:text-gray-900 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-700 dark:hover:text-white"
          >
            <svg
              @click="NextPage()"
              class="w-7 h-7"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                clip-rule="evenodd"
              ></path>
            </svg>
          </div>
          <span class="text-sm font-normal text-gray-500 dark:text-gray-400"
            >查看<span class="font-semibold text-gray-900 dark:text-white">{{
              limit
            }}</span>
            共
            <span class="font-semibold text-gray-900 dark:text-white">{{
              num
            }}</span></span
          >
        </div>
        <div class="flex items-center space-x-3">
          <div
            @click="BackPage()"
            class="inline-flex items-center justify-center flex-1 px-3 py-2 text-sm font-medium text-center text-white rounded-sm bg-indigo-700 hover:bg-indigo-800 focus:ring-4 focus:ring-indigo-300 dark:bg-indigo-600 dark:hover:bg-indigo-700 dark:focus:ring-indigo-800"
          >
            <svg
              class="w-5 h-5 mr-1 -ml-1"
              fill=" currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                clip-rule="evenodd"
              ></path>
            </svg>
            上一页
          </div>
          <div
            @click="NextPage()"
            class="inline-flex items-center justify-center flex-1 px-3 py-2 text-sm font-medium text-center text-white rounded-sm bg-indigo-700 hover:bg-indigo-800 focus:ring-4 focus:ring-indigo-300 dark:bg-indigo-600 dark:hover:bg-indigo-700 dark:focus:ring-indigo-800"
          >
            下一页
            <svg
              class="w-5 h-5 ml-1 -mr-1"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                clip-rule="evenodd"
              ></path>
            </svg>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "flowbite-vue";
import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeletonFilter from "../widgets/LoadSkeletonFilter.vue";
import { onBeforeRouteUpdate } from "vue-router";
export default {
  components: {
    LoadSkeletonFilter,
  },
  setup() {
    const loading = ref(true);
    const toast = useToast();
    const video_type = ref(null);
    const video_types = ref(null);
    const video_tag = ref(null);
    const video_tags = ref(null);
    const video_year = ref(null);
    const video_years = ref(null);
    const data = ref(null);
    const error = ref(null);
    const num = ref(null);
    const page = ref(null);
    const size = ref(null);
    const limit = ref(null);
    const { proxy } = getCurrentInstance();

    video_tags.value = [
      "全部",
      "剧情",
      "犯罪",
      "悬疑",
      "爱情",
      "冒险",
      "动作",
      "喜剧",
      "科幻",
      "惊悚",
      "纪录片",
      "动画",
      "奇幻",
      "恐怖",
      "战争",
      "灾难",
      "同性",
      "儿童",
      "武侠",
      "情色",
    ];
    video_types.value = [
      "最新",
      "电影",
      "美剧",
      "韩剧",
      "日剧",
      "动漫",
      "国产",
    ];
    video_years.value = getPreviousYears(15);
    video_type.value = proxy.$route.params.video_type;
    video_tag.value = proxy.$route.query.video_tag;
    video_year.value = proxy.$route.query.video_year;
    page.value = proxy.$route.query.page;
    size.value = proxy.$route.query.size;
    if (proxy.$route.query.page == undefined) {
      page.value = 1;
      size.value = 24;
    }

    onBeforeRouteUpdate((to) => {
      video_type.value = to.params.video_type;
      video_tag.value = to.query.video_tag;
      video_year.value = to.query.video_year;
      page.value = to.query.page;
      size.value = to.query.size;
      fetchData();
    });

    function getPreviousYears(count) {
      const currentYear = new Date().getUTCFullYear();
      const years = [];

      for (let i = 0; i < count; i++) {
        years.push((currentYear - i).toString());
      }
      years.unshift("全部");
      return years;
    }

    function fetchData() {
      if (video_tag.value == undefined) {
        video_tag.value = video_tags.value[0];
      }
      if (video_year.value == undefined) {
        video_year.value = video_years.value[0];
      }
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/type/tag/year?type=${video_type.value}&tag=${video_tag.value}&year=${video_year.value}&page=${page.value}&size=${size.value}`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            data.value = json.data;
            num.value = json.num;
            let rigth = page.value * size.value;
            if (rigth >= num.value) {
              rigth = num.value;
            }
            limit.value = (page.value - 1) * size.value + "-" + rigth;
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }

    onMounted(() => {
      fetchData();
      document.title = "筛选";
    });

    const typeRef = async () => {
      toast.add({
        type: "success",
        time: 3000,
        text: "获取资源中!",
      });
      fetchData();
    };
    return {
      page,
      size,
      loading,
      toast,
      video_year,
      video_years,
      video_types,
      video_type,
      video_tag,
      video_tags,
      data,
      typeRef,
      limit,
      num,
    };
  },
  methods: {
    refData() {
      this.toast.add({
        type: "success",
        time: 3000,
        text: "获取资源中!",
      });
      this.$router.push({
        path: "/list/" + this.video_type,
        query: {
          video_tag: this.video_tag,
          video_year: this.video_year,
          page: this.page,
          size: this.size,
        },
      });
    },
    changeVideoType(data) {
      this.video_type = data;
      this.page = 1;
      this.refData();
    },
    changeVideoTag(data) {
      this.video_tag = data;
      this.page = 1;
      this.refData();
    },
    changeVideoYear(data) {
      this.video_year = data;
      this.page = 1;
      this.refData();
    },
    BackPage() {
      this.page = parseInt(this.page) - 1;
      if (this.page <= 0) {
        this.toast.add({
          type: "warning",
          time: 3000,
          text: "已经是第一页啦!",
        });
        this.page = 1;
        return;
      }
      this.refData();
    },
    NextPage() {
      this.page = parseInt(this.page) + 1;
      this.refData();
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>
